@-ms-viewport {
  width: device-width; }

*,
::after,
::before {
  box-sizing: border-box; }

html {
  -webkit-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: 100%;
  /* 2 */
  font-size: 15px; }

body {
  margin: 0;
  font-family: Solido, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: 500;
  line-height: 1.5;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-kerning: auto;
  color: #111;
  background-color: #fff; }

figure {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary,
svg {
  display: block; }

img {
  vertical-align: middle;
  max-width: 100%;
  height: auto; }

[role="button"] {
  cursor: pointer; }

a,
area,
button,
[role="button"],
input,
label,
select,
summary,
textarea {
  touch-action: manipulation; }

a {
  cursor: pointer;
  text-decoration: none;
  color: #fff; }
  a:hover {
    color: #eee; }

input,
button,
select,
textarea {
  line-height: inherit; }

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox; }

textarea {
  resize: vertical; }

input[type="search"] {
  -webkit-appearance: none; }

[hidden] {
  display: none !important; }

@font-face {
  font-family: "Solido";
  src: url("../fonts/Solido-Book.woff2") format("woff2"), url("../fonts/Solido-Book.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Solido";
  src: url("../fonts/Solido-Bold.woff2") format("woff2"), url("../fonts/Solido-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

