.shell-header {
  height: 50px;
  background-color: #000;
  display: flex;
  align-items: center;
  justify-content: center; }
  .shell-header__logo {
    width: 120px; }
    @media (min-width: 768px) {
      .shell-header__logo {
        width: 180px; } }
  @media (min-width: 768px) {
    .shell-header {
      height: 60px; } }

.shell-footer {
  padding: 80px 0;
  background-color: #000;
  color: #eee;
  letter-spacing: 0.04em;
  margin-top: 80px; }

.container {
  margin: 0 auto;
  padding: 0 20px;
  max-width: 700px; }
  @media (min-width: 1024px) {
    .container {
      max-width: 1100px; } }

.footer-container {
  display: flex;
  flex-wrap: wrap; }

.footer-item {
  padding: 0 20px;
  flex: 0 0 100%;
  margin-bottom: 20px; }
  @media (min-width: 768px) {
    .footer-item {
      flex: 0 0 50%;
      max-width: 50%; } }
  @media (min-width: 1024px) {
    .footer-item {
      flex: 0 0 25%;
      max-width: 25%; } }

.footer-bottom {
  flex: 0 0 100%;
  max-width: 100%;
  padding: 0 20px; }

h2 {
  font-size: 16px;
  color: #999;
  text-transform: uppercase;
  font-weight: 700; }

ul {
  padding: 0;
  list-style: none; }

li {
  margin-bottom: 5px;
  font-weight: 600; }
